/* ********** Reset CSS ********** */
* {
	margin: 0;
	padding: 0;
	list-style: none;
}
/* ********** Reset box-sizing ********** */


body {
  font-size: 1rem;
}


.App {
  text-align: center;
  min-height: 100vh; }
  .App ul {
    padding-left: 0;
    list-style: none; }
    .App ul a {
      text-decoration: none;
      color: #09d3ac; }
  .App .menu-items.grid {
    text-align: right;
    display: grid;
    grid-template-columns: repeat(4, -webkit-max-content);
    grid-template-columns: repeat(4, max-content);
    grid-column-gap: 1rem;
    align-items: center;
    height: 100%; }
    .App .menu-items.grid a:hover {
      color: #adfbec; }
  .App .navbar {
    background-color: #282c34;
    color: #09d3ac;
    height: 40px; }

