.multiplication-chart__container {
  width: 1280px;
  margin: 0 auto;
  background-color: #f0f0f0; }

.multiplication-chart__items {
  padding: 80px;
  box-sizing: border-box;
  background-color: #f0f0f0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  /* 方法一 第七個以前都加上margin-bottom */
  /* 方法一 第七個以前都加上margin-bottom */
  /* 方法二 自動算出最後一行不要加margin */
  /* 方法二 自動算出最後一行不要加margin */ }
  .multiplication-chart__items__item {
    margin-bottom: 40px; }
  .multiplication-chart__items__item:nth-of-type(3n+1):nth-last-of-type(-n+3),
  .multiplication-chart__items__item:nth-of-type(3n+1):nth-last-of-type(-n+3) ~ .multiplication-chart__items__item {
    margin-bottom: 0px; }

