@charset "UTF-8";
.multiplication-title {
  width: 350px;
  height: 366px;
  box-sizing: border-box;
  color: #2EB738;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .multiplication-title__deco-line > span {
    display: inline-block;
    font-family: "Helvetica Neue";
    width: 280px;
    border: 1px solid #2EB738;
    position: relative; }
    .multiplication-title__deco-line > span::before {
      content: "×";
      text-align: center;
      position: absolute;
      font-size: 24px;
      top: -17px;
      left: -36px; }
    .multiplication-title__deco-line > span::after {
      content: "×";
      text-align: center;
      position: absolute;
      font-size: 24px;
      top: -17px;
      right: -36px; }
  .multiplication-title .p1 {
    font-family: "PingFang TC";
    font-weight: 500;
    font-size: 56px;
    line-height: 84px; }
  .multiplication-title .p2 {
    font-family: "Helvetica Neue";
    font-size: 24px;
    line-height: 36px; }

@charset "UTF-8";
.multiplication-card {
  width: 350px;
  height: 366px;
  box-sizing: border-box;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 10px #D8D8D8;
  border-radius: 100px 0px 30px 0px;
  padding: 64px 40px;
  color: #2EB738;
  font-family: "Helvetica Neue";
  display: flex;
  flex-direction: column;
  flex-wrap: wrap; }
  .multiplication-card__title {
    font-size: 128px;
    height: 50%;
    text-shadow: 4px 3px 0px #f0f0f0;
    /* 文字要對齊上方 方法一 */
    /* 文字要對齊上方 方法二 */
    line-height: 90px; }
  .multiplication-card__formula {
    font-size: 24px;
    line-height: 1;
    text-align: left;
    height: calc(100% / 6); }

